const SEND_MESSAGE = 'SEND-MESSAGE';

let initialState={
    dialogs:[
        {id: 1, name:''},
        {id: 2, name:''},
        {id: 3, name:''},
        {id: 4, name:''},
        {id: 5, name:''},
        {id: 6, name:''}
    ],
    messages: [
        {id: 1, message:'yo hoho'},
        {id: 2, message:'yo'},
        {id: 3, message:'youhaha hoho'},
        {id: 4, message:'yuhoo'},
        {id: 5, message:'haholop'},
        {id: 6, message:'brgh'}
    ]
};
const  dialogsReducer = (state=initialState, action) => {
        switch (action.type) {
            case SEND_MESSAGE:
                let body = action.newMessageBody;
                return  {
                    ...state,
                    messages:[...state.messages, {id: 7, message: body}]
                };
            default:
                return state;
        }
}
export const sendMessageCreator = (newMessageBody) =>({  type: SEND_MESSAGE, newMessageBody})
export default dialogsReducer;